header{
    padding: 40px 4%;
    border-bottom: 2px solid red; 
    background-color: var(--background-color);
    margin: 0 auto;
}

.nav{
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    justify-content: space-between;
    white-space: nowrap;
}

.inicio h2{
    font-size: 30px;
    color: #bfbfbf;
    transition: 0.2s;
}
.inicio h2:hover{
    transform: scale(1.10);
}
.nome h1{
    font-size: 40px;
    color: #bfbfbf;
    margin-right: 10px;
}

.nome h1 span{
    color: red
}

.container{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: var(--background-color);
}

.title{
    font-size: 85px;
    color: #bfbfbf;
    animation: flipTitle 2s;
}

.title span{
    color: var(--secondary-color);
}

@keyframes flipTitle {
    from{
        transform: rotateX(90deg);
    }
    to{
        transform: rotateX(0deg);
    }
}

.containerInput{
    background-color: rgba(255, 255, 255, 0.2);
    padding: 15px;
    margin: 34px 0;
    display: flex;
    border-radius: 8px;
    box-shadow: 1px 3px 8px var(--primary-color2);
}

.containerInput input{
    background-color: transparent;
    border: 0;
    color:#fff;
    outline: none;
    margin-right: 8px;
}

.containerInput input::placeholder{
    color: #bfbfbf;
}

.buttonSearch{
    background-color: transparent;
    border: 0;
    color: #bfbfbf;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: transform 0.5s;;
}

.buttonSearch:hover{
    transform: scale(1.2);
}

.main{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #bfbfbf;
    box-shadow: 1px 3px 8px #000;
    width: 500px;
    border-radius: 8px;
}

.main h2{
    margin: 16px 0;
    font-size: 39px;
}

.main span{
    margin-bottom: 16px;
    font-weight: bold;
}

@media (max-width: 750px){
    .title{
        font-size: 40px;
    }
    .main h2{
        font-size: 28px;
    }

    .main{
        width: 100%;
        min-height: 250px;
    }
    .inicio h2{
        font-size: 25px;
        white-space: normal
    }
    .nome h1{
        font-size: 30px;
    }
}